import { gsap, TimelineMax } from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';

export default class AnimationManager {
	constructor() {
		gsap.registerPlugin(ScrollTrigger);
	}

	initializeAnimation(currentScreen) {
		let delay = 0.75;

		if (currentScreen === 'default') {
			this.setHomeAnimation();
			delay = 7.5;
		} else {
			const html = document.querySelector('html');
			// use Home for splash screen
			gsap.to(html, {
				opacity: 1
			});
		}

		this.setNavbarAnimation(delay);
		this.setAboutAnimation();
		this.setProjectAnimation();
		this.setTimelineAnimation();
		this.setContactAnimation();
	}

	setNavbarAnimation(delay = 0.75) {
		const navbar = document.querySelector('.nav-container');

		const tl = new TimelineMax();

		const html = document.querySelector('html');

		tl
			.from(navbar, {
				delay: delay,
				duration: 0.75,
				top: -100,
				ease: 'ease.out',
				opacity: 0
			})
			.to(html, {
				delay: 1,
				overflowY: 'auto'
			});
	}

	setHomeAnimation() {
		const home = document.querySelector('#home');
		const greetings = document.querySelectorAll('.greeting');
		const viewButton = document.querySelector('.home .view-button');
		const tl = new TimelineMax();

		const html = document.querySelector('html');

		// use Home for splash screen
		gsap.to(html, {
			opacity: 1,
			overflowY: 'hidden'
		});

		tl
			.from([ home ], {
				delay: 0.75,
				duration: 1,
				opacity: 0
			})
			.from([ home ], 0.5, {
				backgroundColor: 'rgba(0,0,0,0)',
				duration: 3
			})
			.from(greetings[0], 1, {
				duration: 0.5,
				opacity: 0
			})
			.from([ greetings[1], greetings[2] ], 1.5, {
				duration: 0.5,
				opacity: 0,
				stagger: {
					amount: 0.3
				}
			})
			.from(greetings[3], 1.4, {
				duration: 0.5,
				opacity: 0
			})
			.from([ viewButton ], 1.5, {
				duration: 1,
				y: 75,
				ease: 'back.out',

				opacity: 0
			});
	}

	setAboutAnimation() {
		const header = document.querySelector('.about .header');
		const divider = document.querySelector('.about .divider');

		gsap.from([ header, divider ], {
			delay: 0.5,
			duration: 0.8,
			ease: 'ease.out',
			x: -100,
			opacity: 0,
			stagger: {
				amount: 0.15
			},
			scrollTrigger: {
				trigger: [ header ],
				start: 'top 75%'
			}
		});

		const aboutMe = document.querySelector('.description');
		const profileHeader = document.querySelector('.profile-header');
		const profile = document.querySelector('.profile');

		gsap.from([ profileHeader, aboutMe ], {
			delay: 0.5,
			duration: 0.8,
			ease: 'ease.out',
			x: -100,
			opacity: 0,
			stagger: {
				amount: 0.15
			},
			scrollTrigger: {
				trigger: [ aboutMe, profileHeader ],
				start: 'top 75%'
			}
		});

		gsap.from([ profile ], {
			delay: 0.5,
			duration: 0.8,
			ease: 'ease.out',
			y: 50,
			opacity: 0,
			stagger: {
				amount: 0.15
			},
			scrollTrigger: {
				trigger: [ profile ],
				start: 'top 75%'
			}
		});

		const aboutSkills = document.querySelector('.about-skills');
		const skillItems = document.querySelectorAll('.skill-item');

		gsap.from([ aboutSkills ], {
			delay: 0.5,
			duration: 0.8,
			ease: 'back.out',
			transformOrigin: 'center',
			scaleX: 0.8,
			scaleY: 0.8,
			opacity: 0,
			scrollTrigger: {
				trigger: [ aboutSkills ],
				start: 'top 75%'
			}
		});
		gsap.from(skillItems, {
			delay: 2,
			duration: 0.6,
			// ease: 'ease.inout',
			ease: 'back.out',
			scaleX: 0.9,
			scaleY: 0.9,

			y: 50,
			opacity: 0,
			stagger: {
				amount: 0.5
			},
			scrollTrigger: {
				trigger: skillItems,
				start: 'top 75%'
			}
		});

		const aboutTechCaption = document.querySelectorAll('.about-tech .caption');
		const aboutTechImages = document.querySelectorAll('.about-tech .sub-panel');

		gsap.from([ aboutTechCaption ], {
			delay: 0.5,
			duration: 0.8,
			ease: 'ease.out',
			y: 50,
			opacity: 0,
			stagger: {
				amount: 0.15
			},
			scrollTrigger: {
				trigger: [ aboutTechCaption ],
				start: 'top 75%'
			}
		});

		let movementVal = 0;

		for (let i = 0; i < aboutTechImages.length; i++) {
			if (i % 2 === 0) {
				movementVal = 50;
			} else {
				movementVal = -50;
			}

			gsap.from([ aboutTechImages[i] ], {
				delay: 0.5,
				duration: 0.8,
				ease: 'ease.out',
				x: movementVal,
				opacity: 0,
				stagger: {
					amount: 0.15
				},
				scrollTrigger: {
					trigger: [ aboutTechImages[i] ],
					start: 'top 75%'
				}
			});

			const techStackImages = aboutTechImages[i].querySelectorAll('.tech-stack-image');
			gsap.from(techStackImages, {
				delay: 1.5,
				duration: 0.6,
				ease: 'ease.out',
				y: 50,
				opacity: 0,
				stagger: {
					amount: 0.5
				},
				scrollTrigger: {
					trigger: techStackImages,
					start: 'top 75%'
				}
			});
		}
	}

	setProjectAnimation() {
		const header = document.querySelector('.project .header');
		const divider = document.querySelector('.project .divider');

		const panels = document.querySelectorAll('.project .panel');

		gsap.from([ header, divider ], {
			delay: 0.5,
			duration: 0.8,
			ease: 'ease.out',
			x: 100,
			opacity: 0,
			stagger: {
				amount: 0.15
			},
			scrollTrigger: {
				trigger: '.project .header',
				start: 'top 75%'
			}
		});

		for (let i = 0; i < panels.length; i++) {
			const subPanelLeft = panels[i].querySelector('.sub-panel.left');
			gsap.from([ panels[i] ], {
				delay: 0.5,
				duration: 0.8,
				ease: 'ease.out',
				y: 50,
				// scaleX: 0.9,
				// scaleY: 0.9,
				opacity: 0,
				stagger: {
					amount: 0.15
				},
				scrollTrigger: {
					trigger: [ panels[i] ],
					start: 'top 85%'
				}
			});
			gsap.from(subPanelLeft.children, {
				delay: 2,
				duration: 0.5,
				ease: 'ease.out',
				y: 50,
				opacity: 0,
				stagger: {
					amount: 0.5
				},
				scrollTrigger: {
					trigger: subPanelLeft,
					start: 'top 85%'
				}
			});
		}
	}

	setTimelineAnimation() {
		const header = document.querySelector('.timeline .header');
		const divider = document.querySelector('.timeline .divider');

		const panel = document.querySelectorAll('.timeline .panel');

		const timelineEvents = document.querySelectorAll('.timeline .timeline-event');

		const timelineEventIcons = document.querySelectorAll('.timeline .timeline-event-icon');

		for (let i = 0; i < timelineEventIcons.length; i++) {
			gsap.from([ timelineEventIcons[i] ], 0.7, {
				rotation: -680,
				scrollTrigger: {
					trigger: [ timelineEventIcons[i] ],
					start: 'top 75%'
				}
			});
		}

		gsap.from([ header, divider ], {
			delay: 0.5,
			duration: 0.8,
			ease: 'ease.out',
			x: -100,
			opacity: 0,
			stagger: {
				amount: 0.15
			},
			scrollTrigger: {
				trigger: '.timeline .header',
				start: 'top 75%'
			}
		});

		gsap.from([ panel ], {
			delay: 0.5,
			duration: 0.8,
			ease: 'ease.out',
			opacity: 0,
			stagger: {
				amount: 0.15
			},
			scrollTrigger: {
				trigger: [ panel ],
				start: 'top 75%'
			}
		});

		for (let i = 0; i < timelineEvents.length; i++) {
			gsap.from([ timelineEvents[i] ], {
				delay: 0.5,
				duration: 0.8,
				// ease: 'Linear.easeNone',
				// scaleY: 0.8,

				ease: 'ease.out',
				y: 50,
				opacity: 0,
				stagger: {
					amount: 0.15
				},
				scrollTrigger: {
					trigger: [ timelineEvents[i] ],
					start: 'top 75%'
				}
			});

			const duties = timelineEvents[i].querySelector('.timeline-duties');
			gsap.from(duties.children, {
				delay: 3,
				duration: 1,
				ease: 'power3.out',
				x: 100,
				opacity: 0,
				stagger: {
					amount: 0.5
				},
				scrollTrigger: {
					trigger: [ duties ],
					start: 'top 75%'
				}
			});

			const techStackImages = timelineEvents[i].querySelectorAll('.tech-stack-image');
			gsap.from(techStackImages, {
				delay: 1.5,
				duration: 0.6,
				ease: 'ease.out',
				y: 50,
				opacity: 0,
				stagger: {
					amount: 0.3
				},
				scrollTrigger: {
					trigger: techStackImages,
					start: 'top 100%'
				}
			});
		}
	}

	setContactAnimation() {
		const header = document.querySelector('.contact .header');
		const divider = document.querySelector('.contact .divider');
		const caption = document.querySelector('.contact .caption');

		gsap.from([ header, divider, caption ], {
			delay: 0.5,
			duration: 0.8,
			ease: 'ease.out',
			x: -100,
			opacity: 0,
			stagger: {
				amount: 0.15
			},
			scrollTrigger: {
				trigger: [ header ],
				start: 'top 75%'
			}
		});

		const contactItems = document.querySelector('.contact .contact-items');
		const contactLinks = document.querySelectorAll('.contact .contact-links');
		const contactForm = document.querySelector('.contact-form');

		gsap.from(contactItems, {
			delay: 0.5,
			duration: 0.8,
			ease: 'ease.out',
			x: -50,
			opacity: 0,
			stagger: {
				amount: 0.5
			},
			scrollTrigger: {
				trigger: contactItems,
				start: 'top 75%'
			}
		});

		gsap.from(contactLinks, {
			delay: 0.5,
			duration: 0.8,
			ease: 'ease.out',
			opacity: 0,
			stagger: {
				amount: 0.5
			},
			scrollTrigger: {
				trigger: contactLinks,
				start: 'top 75%'
			}
		});

		gsap.from([ contactForm ], {
			delay: 0.5,
			duration: 0.8,
			ease: 'ease.out',
			y: 50,
			opacity: 0,
			stagger: {
				amount: 0.15
			},
			scrollTrigger: {
				trigger: [ contactForm ],
				start: 'top 75%'
			}
		});
	}
}
